* {
    font-family: Helvetica, Arial, sans-serif;
}

body {
    padding: 60px;
    background-color: #001935;
    color: white;
}

input {
    color: blueviolet;
    width: 100%;
    font-size: 1.2rem;
}

.card-grid {
    display: flex;
    box-sizing: content-box
}

.card {
    vertical-align: top;
    display: inline-block;
    /*  float: left; */
    border: 1px solid grey;
    box-sizing: border-box;
    border-radius: 7px;
    margin-bottom: 20px;
    margin-right: 10px;
    margin-top: 3px;
    overflow: hidden;
    width: 25%;
    background-color: black;
}

.card p {
    font-family: cursive;
    font-size: 10px;
    padding: 2px;
    text-align: center;
}

.card img {
    width: 100%;
    height: 160px;
    object-fit: cover;
}